.board_info {
  display: -ms-flexbox;
  display: flex;
}

.game_settings {
  width: 40%;
}

.game_settings label {
  clear: both;
  display: block;
  margin-bottom: 1ex;
  padding-left: 2em;
  text-align: left;
  width: 70%;
}

.game_settings input {
  float: right;
}

.game_settings div,
.game_status div,
