  margin-top: 0;
}

.game_status {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.player_info {
  border: 1px solid #aaa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}



